___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --background-color: #333;
  --text-color: #fff;
  --button-color: #007bff;
  --button-hover-color: #0056b3;
  --form-background: rgba(0, 0, 0, 0.9);
  --input-background: #555;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 40px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

#weatherForm {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px; /* Adds gap between elements */
  max-width: 500px;
  margin: 0 auto;
  background-color: var(--form-background);
  padding: 20px;
  border-radius: 10px;
}

label {
  display: block;
  margin-bottom: 10px;
}

input[type='text'] {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: var(--input-background);
  color: var(--text-color);
}

button {
  width: 100%;
  padding: 10px;
  border: none;
  background-color: var(--button-color);
  color: var(--text-color);
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: var(--button-hover-color);
}

.main {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
h1 {
  font-size: 4em;
}
label {
  font-size: 2em;
}
.weather-icon {
  width: 5em;
  height: 5em;
}
